import css from './Info.module.scss'
const Info = () => {
	return (
		<div className={css.info}>
			<h2 className={css.header}></h2>
			<ul className={css.list}>
				<li className={css.item}>
					<p className={css.text}> </p>
				</li>
				<li className={css.item}>
					<p className={css.text}></p>
				</li>
				<li className={css.item}>
					<p className={css.text}>  </p>
				</li>
				<li className={css.item}>
					<p className={css.text}>  </p>
				</li>
				<li className={css.item}>
					<p className={css.text}> </p>
				</li>
			</ul>
		</div>
	)
}
export default Info
